</template>

<script>
import $ from 'jquery';
import Pagination from '../../components/Pagination.vue';

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      coupons: {},
      pagination: {},
      tempCoupon: {},
      isNew: false,
    };
  },
  methods: {
    getCouponData(page = 1) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_ZACPATH}/admin/coupons?page=${page}`;
      vm.$store.dispatch('updateLoading', true);
      vm.$http.get(api).then((response) => {
        vm.$store.dispatch('updateLoading', false);
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempCoupon = {};
        this.isNew = true;
        $('#couponModal').modal('show');
      } else {
        const tempCouponCopy = {};
        this.tempCoupon = Object.assign(tempCouponCopy, item);
        this.isNew = false;
        $('#couponModal').modal('show');
      }
    },
    updateCoupon() {
      const vm = this;
      let httpMethod = 'post';
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_ZACPATH}/admin/coupon`;
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_ZACPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }
      vm.$http[httpMethod](api, { data: vm.tempCoupon }).then((response) => {
        if (response.data.success) {
          $('#couponModal').modal('hide');
          vm.getCouponData();
          vm.$store.dispatch('updateMessage', { message: response.data.message, status: 'success' });
        } else {
          vm.$store.dispatch('updateMessage', { message: response.data.message, status: 'danger' });
        }
      });
    },
    opendelCouponModal(item) {
      const tempCouponCopy = {};
      this.tempCoupon = Object.assign(tempCouponCopy, item);
      $('#delCouponModal').modal('show');
    },
    delCoupon() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_ZACPATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.$http.delete(api, { data: vm.tempCoupon }).then((response) => {
        if (response.data.success) {
          $('#delCouponModal').modal('hide');
          vm.getCouponData();
          vm.$store.dispatch('updateMessage', { message: response.data.message, status: 'danger' });
        } else {
          vm.$store.dispatch('updateMessage', { message: response.data.message, status: 'danger' });
        }
      });
    },
  },
  created() {
    this.getCouponData();
  },
};
</script>
